import { Button, Input } from '@ya.praktikum/react-developer-burger-ui-components'
import styles from './forms.module.css'
import { useState } from 'react'
import { Link, Redirect, useHistory, useLocation } from 'react-router-dom'
import { useResetPasswordMutation } from '../../services/api'
import { useForm } from '../../hooks/use-form'

export default function ResetPassword() {

  const [resetPassword] = useResetPasswordMutation()
  const { handleSubmit, register } = useForm({
    password: '',
    token: ''
  })

  const location = useLocation()
  const { push } = useHistory()

  const submit = async resetData => {
    const result = await resetPassword(resetData)

    if (result.data) result.data.success && push('/login')
  }

  const [visiblePassword, setVisiblePassword] = useState(false)

  const toggle = () => { setVisiblePassword(prev => !prev) }

  if (!(location.state?.from?.pathname === '/forgot-password')) return <Redirect to="/forgot-password" />

  return (
    <main className={styles.main}>
      <form onSubmit={handleSubmit(submit)} className={styles.fields}>
        <h2 className="text text_type_main-medium"> </h2>
        <Input 
          {...register('password')}
          placeholder="  " 
          icon="ShowIcon"
          onIconClick={toggle} 
          type={visiblePassword ? 'text' : 'password'} 
        />
        <Input {...register('token')} placeholder="   " type="text" />
        <Button htmlType="submit"></Button>
      </form>
      <div className={styles.links}>
        <p className="text text_type_main-default text_color_inactive">
           ? <Link to="/login" className="text text_type_main-default text_color_accent"></Link>
        </p>
      </div>
    </main>
  )
}