import { useSelector } from 'react-redux'
import { useCreateOrderMutation } from '../../services/api'
import styles from './order-details.module.css'
import { useEffect } from 'react'
import { useActions } from '../../hooks/use-actions'

export default function OrderDetails() {
  const { bun, fillings } = useSelector(state => state.currentBurger)
  const { clearIngredients } = useActions()
  const ingredientsList = [
    bun._id,
    ...fillings.map(filling => filling._id),
    bun._id
  ]
  const [order, { data: burger, isError, isFetching }] = useCreateOrderMutation()

  useEffect(() => {
    order(ingredientsList)
    clearIngredients()
  }, [])

  if (isError) return <div className={styles.details}></div>

  return (
    <div className={styles.details}>
      <p className={`text text_type_digits-large pt-4 pb-8 ${styles.id}`}>{isFetching || !burger ? '...' : burger.order.number}</p>
      <p className="text text_type_main-medium pb-15"> </p>
      <div className={`pb-15 ${styles.confirm}`}>
        <img src="/react-stellar-burger/images/confirm-1.svg" alt="-1" className={styles.decoration} />
        <img src="/react-stellar-burger/images/confirm-2.svg" alt="-2" className={styles.decoration} />
        <img src="/react-stellar-burger/images/confirm-3.svg" alt="-3" className={styles.decoration} />
        <img src="/react-stellar-burger/images/confirm.svg" alt="" className={styles.decoration} />
      </div>
      <p className="pb-2 text text_type_main-default">   </p>
      <p className="pb-30 text text_type_main-default text_color_inactive">    </p>
    </div>
  )
}